"use client";
import Image from "next/image";
import { useState, useEffect } from "react";

interface Slide {
  src: string;
  title: string;
  description: string;
}

const slides: Slide[] = [
  {
    src: "/images/beach.jpg",
    title: "Tropical Escapes",
    description: "Relax on pristine beaches and let the waves wash your worries away.",
  },
  {
    src: "/images/mountain.jpg",
    title: "Majestic Mountains",
    description: "Breathe in the crisp mountain air and explore breathtaking peaks.",
  },
  {
    src: "/images/city.jpg",
    title: "Urban Adventures",
    description: "Experience vibrant cultures and modern skylines across the world.",
  },
];

export default function HeroSlider() {
  const [current, setCurrent] = useState(0);
  const length = slides.length;

  // Auto-rotate timer
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % length);
    }, 5000); // Change every 5 seconds
    return () => clearInterval(timer);
  }, [length]);

  const nextSlide = () => setCurrent((current + 1) % length);
  const prevSlide = () => setCurrent((current - 1 + length) % length);

  return (
    <div className="relative w-full h-[90vh] overflow-hidden">
      {slides.map((slide, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-1000 ease-in-out ${
            index === current ? "opacity-100 z-10" : "opacity-0 z-0"
          }`}
        >
          <Image
            src={slide.src}
            alt={slide.title}
            fill
            className="object-cover"
            priority={index === 0}
          />
          {/* Overlay */}
          <div className="absolute inset-0 bg-black/40 flex flex-col justify-center items-center text-center px-6">
            <h2 className="text-5xl md:text-6xl font-bold text-white drop-shadow-lg">
              {slide.title}
            </h2>
            <p className="mt-4 max-w-2xl text-lg text-gray-200 drop-shadow-md">
              {slide.description}
            </p>
          </div>
        </div>
      ))}

      {/* Controls */}
      <button
        onClick={prevSlide}
        className="absolute left-6 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white p-3 rounded-full"
      >
        ❮
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-6 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white p-3 rounded-full"
      >
        ❯
      </button>

      {/* Dots */}
      <div className="absolute bottom-6 left-1/2 -translate-x-1/2 flex space-x-2">
        {slides.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrent(index)}
            className={`w-3 h-3 rounded-full ${
              index === current ? "bg-white" : "bg-gray-400"
            }`}
          />
        ))}
      </div>
    </div>
  );
}
